
.sidebar-filter__link{
	display: block;
	padding-top: rem(14px);
	padding-bottom: rem(10px);
	background-color: #f1f1f1;
	border-bottom: 1px solid #000;
	font-size: rem(18px);
	font-family: $ave-bold;
	text-align: center;
	color: #000;
	text-transform: uppercase;

	&:hover, &:active, &:focus{
		color: #000;
		text-decoration: none;
	}
	
}
.sidebar-filter__item{
	margin-bottom: rem(24px);
	.sidebar-filter__link:last-child{
		border-top: 1px solid #000;
		border-bottom: none;
	}
}
.sidebar-filter__item .sidebar-filter__link.filter-link-bottom{
	border-top: none;
	padding-top: 10px;
	&--active{
		border-top: 1px solid #000;

		.ico-chevron-down-small{
			transform: rotate(180deg);
		}
	}
}
.sidebar-filter__sublist{
	padding: rem(20px) rem(10px) 12px rem(10px);
	border-left: 1px solid #f1f1f1;
	border-right: 1px solid #f1f1f1;
}
.ico-chevron-down-small{
	@include sprite($ico-chevron-down-small);
	display: inline-block;
}
.ico-birdie{
	
	display: none;
	@include sprite($ico-birdie);
	position: absolute;
	@include absolute-center;
}
.sidebar-filter__subitem{
	margin-bottom: 7px;
	label{
		position: relative;
		
		.input-custom{
			position: absolute;
			 left: -9999px;
			 &:checked + .sidebar-filter__custom-checkbox .ico-birdie{
			 	display: block;
			 }
		}

	}
}
.sidebar-filter__custom-checkbox{
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 17px;
	height: 17px;
	border: 1px solid #a1a1a1;

	& + span{
		display: inline-block;
		vertical-align: middle;
	margin-left: 7px;
	font-family: $avenir-med;
	font-size: rem(13px);
		text-transform: uppercase;
	}
}
.sidebar-filter__checked{
	.sidebar-filter__custom-checkbox + span{
		font-family: $ave-bold;
	}
}
.sidebar-filter__subitem-row{
	position: static;
	left: 0;
}
.sidebar-filter__subitem-fields{
	display: inline-block;
	vertical-align: top;
	width: 83%;
	margin-left: 3px;
}
.sidebar-filter__subitem-row{
	input, span{
		display: inline-block;
		vertical-align: middle;
		text-transform: uppercase;
		font-size: rem(13px);
	}
	input{
		padding: 0 5px;
		width: 77%;
		margin-bottom: 5px;
		border: 1px solid #000;
	}
	span{
		width: 20.4833px;
	}
}