.submenu-shop{
	margin-top: -60px;
	margin-bottom: rem(32px);
	.submenu-shop__list{
		font-size: 0;
	}
	.submenu-shop__item{
		display: inline-block;
		vertical-align: top;
		border-right: 1px solid #fff;
	}
	.submenu-shop__item--active{
		.submenu-shop__link{
			background-color: #fff;
		}
	}
	.submenu-shop__link{
			display: block;
			padding: rem(19px) rem(16px);
			font-size: rem(16px);
			font-family: $avenir-med;
			background-color: #f1f1f1;
			text-transform: uppercase;
			text-decoration: none;
			text-align: center;
			color: #000;
			&:hover, &:active, &:focus{
				background-color: #fff;
			}
			
	}
}
.submenu-shop--looks{
	.submenu-shop__item--active{
		.submenu-shop__link{
			background-color: rgba(#88fb18, .7);
		}
	}
}
.block-select{
	@include clearfix;
	margin-bottom: rem(29px);

	.sect-title-wrap{
		float: left;
	}
}
.filter-select{
	float: right;

	.ik_select.select-color, .ik_select.select-size, .ik_select.select-range{
		width: 205px !important;
		border: 1px solid #000;

		.ik_select_link_text{
			
			padding: 9px 22px;
		}
		.ik_select_link_text:after{
			top: 10px;
			right: 18px;
		}
	}
}

.select-color-dropdown, .select-size-dropdown, .select-range-dropdown{
		
		width: 205px !important;
		border: 1px solid #000;
		padding: 20px 22px;
		left: -1px;
		background-color: #fff;

		.ik_select_hover{
			font-family: $ave-bold;
		}
		.ik_select_option:first-child{
			display: none;
		}

	}

.select-size-dropdown{
	.ik_select_option:last-child{
		font-family: $ave-bold;
		text-transform: uppercase;
	}
}
.filter-select__cancel{
	font-size: rem(14px); 
	color: #808080;
	text-transform: uppercase;
}
.cancel-wrap{
	margin-top: 12px;
	text-align: right;
}
.shop-product__item-last{
	position: relative;
}
.shop-product__item-last-inner{
	position: absolute;
	width: 100%;
	height: 100%;
	display: block;
	text-align: center;
	span.ico-next{
		display: inline-block;
		margin-bottom: 25px;
		@include sprite($ico-next-page);
	}
	
	&:after{
		content: '';
		display: inline-block;
		height: 100%;
		width: 1px;
		visibility: hidden;
		vertical-align: middle;
		overflow: hidden;
	}
	&:hover{
		.next-center{
			color: #000;
		}
		
	}
	
}
.next-center{
	display: inline-block;
	vertical-align: middle;
	color: #808080;
	font-size: 12px;
	font-family: $ave-bold;
	text-transform: uppercase;
	div{
		text-decoration: underline;
	}
}
