.product-card__top-article-number,
.product-card__top-stylist{
	display: inline-block;
}
.product-card__top-stylist{
	margin-left: rem(33px);
}

.look-description{

	.product-card__top-article{
		margin-top: rem(18px);
		text-transform: none;
		color: #757575;
		
		.article-number,
		.stylist-name{
			text-transform: uppercase;
			color: #000;
			margin-left: 0;
		}
	}

	.product-card__top-description{
		font-size: rem(17px);
		line-height: rem(24px);
	}
	
	.size-table-link{
		margin-left: 0;
		font-size: rem(15px);
	}
	.product-card__top-prices{
		font-size: rem(20px);
		font-family: $ave-bold;
	}
	.product-card__top-sale{
		margin-left: rem(19px);
	}
	.add-to-cart{
		display: inline-block;
		margin-left: rem(20px);
		padding: rem(10px) rem(20px);
		padding-top: rem(13px);
		font-family: $ave-reg;
		letter-spacing: 1.4px;
		border-width: 2px;
		font-size: rem(14px);
	}
}
.product-card__top{
	.look-description{
		.add-to-cart{
			width: auto;

		}
	}
}
.look-parameters__item{

	border-top: 1px solid #000;
}
.look-parameters__link{
	position: relative;
	padding: rem(12px) 0 rem(12px) rem(11px);
	display: block;
	@include clearfix;
	font-family: $avenir-med;
	font-size: rem(17px);
	color: #000;
	text-decoration: none;
	text-transform: uppercase;

	&:hover, &:active, &:focus{
		text-decoration: none;
		color: #000;
	}
	

	span{
		display: inline-block;
		vertical-align: middle;
		margin-left: rem(17px);
	}
}
.look-parameters__ico-wrap{
	display: inline-block;
	vertical-align: middle;
	width: 32px;
	text-align: center;
}
.look-parameters__ico{
	display: inline-block;
	vertical-align: middle;
}
.ico-eye{
	@include sprite($ico-eye);


}
.ico-dummy{
	@include sprite($ico-dummy);


}
.ico-flateron{
	@include sprite($ico-flateron);


}
.look-parameters__link-chevron{
	position: absolute;
	display: inline-block;
	@include sprite($ico-slidedown-black);
	@include absolute-center;
	right: 0;
	left: auto;
}
.parameters__sublist{
	padding-top: rem(10px);
	padding-bottom: rem(40px);
}
.parameters__sublist{
	// display: block;
}
.look-parameters__unit{
	padding-right: rem(37px);
	padding-top: rem(12px);
	padding-bottom: rem(12px);
	border-top: 1px solid $border;
}

.look-parameters__unit-top{
	text-align: justify;
	line-height: 0;
	position: relative;

	.ico-close{
		@include absolute-center;
		left: auto;
		right: rem(-30px);
		transform: scale(.8);
		opacity: .5;
		top: -18%;
	}
	&:after{
		@extend .justify;
	}
}
.look-parameters__unit-header{
	position: relative;
	color: #b3b3b3;
	font-size: rem(15px);
	text-align: justify;
	&:after{
		@extend .justify;
	}
	div{
		display: inline-block;
		text-align: left !important;
		&:first-child{
			min-width: 35.76%;
		}
	}
}
.look-parameters__unit-name,
.look-parameters__unit-select,
.look-parameters__unit-price{
	display: inline-block;
	vertical-align: middle;
	line-height: 1.5;
}
.look-parameters__unit-name{
	width: 40%;
	color: #000;
	text-decoration: none;
	&:hover{
		color: #000;
	}
}
.look-parameters__unit-thing{
	font-size: rem(17px);
}
.look-parameters__unit-designer{
	font-size: rem(16px);
	text-transform: uppercase;
}
.look-parameters__unit-select{
	width: 12%;
	font-size: rem(16px);
	font-family: $avenir-med;
}
.look-parameters__unit-price{
	width: 31%;
	text-align: right;
	font-size: rem(20px);
}
.look-parameters__unit-color{
	font-size: rem(15px);
	color: #9b9a9a;
}
.look-parameters__unit-select{
}
.ik_select.look-parameters{
	margin-left: 0;
		width: 76px !important;
		border: 1px solid $border;

	.ik_select_link_text{
		padding: 7px 14px;
	}
	.ik_select_link_text::after{
		@include sprite($ico-chevron-down-small);
		@include absolute-center;
		transform: none;
		display: block;
		left: auto;
		right: 12px;
	}
}
.ik_select_link.look-parameters-link{
		position: relative;
}
.ik_select_dropdown.look-parameters-dropdown{
	width: 76px !important;
	padding: 7px 14px;
	background-color: #fff;

}
.look-parameters__content{
	font-size: rem(17px);
	color: #808080;
}
.fotorama-custom-nav__item{
	max-width: 	105px;
	display: inline-block;
	vertical-align: top;	
	img{
		width: 100%;
	}
}
.fotorama-wrap{
	padding-right: rem(36px);
}
.look-socials{
	margin-left: rem(32px);;
	@extend .socials-list;
	display: block !important;
	li{
		display: inline-block;

	}
}
.look-share {
	margin-left: rem(32px);;
	margin-bottom: rem(4px);
	font-size: rem(16px);
	text-transform: uppercase;
	color: #999999;
	font-family: $avenir-med;
}
// Fotorama--------------------
.fotorama__nav{
	text-align: left;
}
.fotorama__nav__frame:last-child{
	position: relative;	
	.fotorama__thumb{
		background-color: transparent;
		border: 3px solid #000;
	}
	img{
		display: none;	
	}
	&:before{
		content: '';

background-image: url(../img/sprite.png);

background-position: -208px -130px;

width: 21px;

height: 28px;

top: 0;

left: 0;

right: 0;

bottom: 0;

margin: auto;

position: absolute;
	}
}
.fotorama__nav__frame .fotorama__video-play{
	display: none;
}
.fotorama__stage__frame--video{
	.fotorama__video-play{
		background-image: url(../img/fotorama.png);
	}
}

