.header{
	margin-bottom: rem(60px);
}
.header__top{
	margin-bottom: rem(13px);
	//padding-top: 18px;
}
.header__relative{
	position: relative;
}
.header__static{
	position: static;
}
.header__info-left{
	float: left;
}
.header__info-right{
	float: right;
}
.header__logo-wrap{
	max-width: 215px;
	display: inline-block;
	padding-top: 18px;
}
.header__info-right{
	padding-top: 30px;
	padding-bottom: 23px;
	padding-left: 8px;
	padding-right: 8px;
	background-color: $bg-gray;
}
.header__info{
	@include clearfix;
	margin-left: -22px;
	text-transform: uppercase;
	font-family: $avenir-med;
	font-size: 11px;
	line-height: 17px;
}
.header__info-left{
	padding-top: 30px;
}
.header__top-item{
	display: inline-block;
	margin-left: 22px;
}
.header__top-ico, .header__top-text{
	display: inline-block;
	vertical-align: middle;
	
}
.header__top-text{
	margin-left: 5px;
}
.top-phone{
	@include sprite($ico-phone);
}
.top-clock{
	@include sprite($ico-clock);
}
.top-mail{
	@include sprite($ico-mail);
}
.top-user{
	@include sprite($ico-user);
}
.top-cart{
	@include sprite($ico-cart-big);
	vertical-align: middle;	
	position: relative;
	margin-top: -7px;
}
.ico-cart{
	@include sprite($ico-cart);
	display: inline-block;
	vertical-align: middle;
}
.header__reg-ico, .header__reg-links{
	display: inline-block;

}
.header__reg-links{
	a{
		color: #000;
	}
}
.header__enter, .header__cart{
	display: inline-block;
}
.header__cart{
	margin-left: 10px;
}
.header__cart-count{
	position: absolute;
	display: block;
	line-height: 40px;
	text-align: center;
	@include absolute-center;
	font-size: 12px;
	color: #fff;
}
.green{
	background-color: #C3FD8B;
}

.header__bottom{
	background-color: #000;

	nav{
		display: inline-block;
	}

}
.header__bottom--abs{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 9999;
}
.header__search{
		display: inline-block;

	.header__search-inner{
		position: relative;
	}
		input[type="text"]{
			width: 116px;
			padding-left: 30px;
			position: relative;
			border-radius: 12px;
	
	background-color: #fff;
	border: none;
		}
		input[type="submit"]{
			position: absolute;
			left: 9px;
			top: 5px;
			font-size: 0;
			@include sprite($ico-loupe);
			background-repeat: no-repeat;
			border: none;
			background-color: transparent;
		}
}

.header__bottom-descr{
	padding: 9px 0;
	font-size: 12px;
	text-transform: uppercase;
	color: #fff;
	
		
}
.header-menu{
	font-size: 0;
}
.header-menu__item{
	display: inline-block;

	&--active{
		.header-menu__link{
			background-color: #fff;
			text-decoration: none;
			color: #000;
		}
	}
}
.header-menu__link{
	display: block;
	padding: 9px 15px;
	font-size: 12px;
	text-transform: uppercase;
	color: #fff;
	//border-right: 1px solid #fff;
	border-left: 1px solid #fff;

	&:hover, &:active, &:focus{
		background-color: #fff;
			text-decoration: none;
			color: #000;
	}
	
}

.popup-cart{
	width: 357px;
	@include clearfix;
	padding: rem(30px) rem(32px) rem(19px) rem(28px);
	background-color: $footer-bg;
}
.popup.popup-cart__title{
	@include clearfix;
	margin-bottom: rem(16px);
	span{
		display: inline-block;
		vertical-align: middle;
		font-family: $avenir-med;
		font-size: rem(18px);
	}
	span.popup-cart__header{

	}
}
.popup-cart__count{
	position: static;
	line-height: auto;
	color: #000;
}
.popup-cart__header{
	text-transform: uppercase;
	margin-top: 4px;
	margin-left: rem(15px);
	margin-right: rem(5px);
}
.popup.popup-cart__title-left{
	float: left;
}
.popup.popup-cart__look-link{
	float: right;
	margin-top: 3px;
	font-size: rem(15px);
	color: #919191;
	text-transform: none;
	text-decoration: underline;
}
.popup-cart__content{
	padding: rem(24px) rem(20px) 0 rem(27px);
	margin-bottom: rem(12px);
	background-color: #fff;
}
.popup-cart__item{
	@include clearfix;
	padding-bottom: rem(23px);
	margin-bottom: rem(23px);
	border-bottom: 1px solid $border;

	&:last-child{
		border-bottom: none;
		margin-bottom: 0;
	}
	span{
		color: #b2b2b2;
		font-size: rem(16px);
		margin-right: rem(10px);
		font-family: $avenir-med;
	}
}
.popup-cart__img{
	float: left;
	max-width: 70px;
}
.popup-cart__item-text{
	float: left;
	width: 60%;
	margin-left: rem(20px);
	text-transform: none;
}
.popup-cart__item-title{
	margin-bottom: rem(12px);
	font-size: rem(17px);
	font-family: $avenir-med;
}
.popup-cart__item-price{
	margin-top: rem(12px);
	font-size: rem(19px);
	font-family: $avenir-med;
	color: #000;
	span{
		margin-left: 5px;
		color: #000;
		font-family: $rubl;
	}
}
.popup-cart__summary{
	float: right;
	padding: 3px;
	margin-bottom: rem(15px);;
	font-family: $avenir-med;
	font-size: rem(16px);
	text-align: right;

	& > span{
		margin-right: 10px;
	}
}
.popup-cart__summ{
	font-size: rem(19px);
	.ruble{
		margin-right: -10px;
		margin-left: 10px;
	}
}