	table{
		width: 100%;
		font-weight: $ave-reg;
		text-align: center;
		tr, td, th{
			
		// border: 1px solid #000;
		}
		tr:nth-child(even){
			background-color: #fff;
		}
		td{
			text-transform: uppercase;
