.popup-sizes__title{
	padding-bottom: rem(14px);
	margin-bottom: rem(15px);
	text-align: center;
	text-transform: uppercase;
	font-size: rem(18px);
	border-bottom: 1px solid #000;
	span.gray-text{
		margin-left: 3px;
		font-size: rem(15px);
	}
