.category{
	margin-top: 5.6rem;
}
.category-item{
	position: relative;
	// padding-left: 48px;
}
.category-item-main .category-big-trapezoid{
	height: 689px;
	width: 100%;
	border: 10px solid #000;
	transform: skewY(7deg);
	border-bottom: none;
	position: relative;


	&:after{
		content: '';
		display: block;
		height: 65px;
		width: 100%;
		position: absolute;
		bottom: -40px;
		left: -10px;
		border: 10px solid #000;
		border-top: none;
		transform: skewY(-7deg);
		box-sizing: content-box;
	}
}
.category-item-main .category-small-trapezoid{
	height: 654px;
	width: 84%;
	border: 3px solid #000;
	transform: skewY(-14deg);
	border-top: none;
	position: absolute;
	top: 40px;
	right: -5%;
	background-color: #fff;
	z-index: 10;

	&:after{
		content: '';
		display: block;
		height: 65px;
		width: 100%;
		position: absolute;
		top: -35px;
		right: -3px;
		border: 3px solid #000;
		border-bottom: none;
		transform: skewY(7deg);
		box-sizing: content-box;
		background-color: #fff;
	}
}
.category-item-main .category-small-trapezoid__img{
	position: absolute;
	max-width: 427px;
	z-index: 5;
	top: -20px;
	left: -52px;
	transform: skewY(7deg);
}
.category-pentagramm-wrap{
	position: absolute;
	right: -9%;
	bottom: 10%;
	transition: all 0.2s ease;
	z-index: 20;
	width: 222px;
	height: 232px;
	span{
		position: relative;
		display: block;
		z-index: 10;
	}
	&:active{
		transform: translateX(6px);
	}
}
