.category-pentagramm-border{
	top: 18px;
	left: 7px;
}
.category-pentagramm__text{
	position: absolute;
	left: 35px;
	top: 87px;
	z-index: 20;

	div{
		font-family: $ave-bold;
		font-size: 24px;
		line-height: 26px;
		color: #fff;
		font-weight: bold;
		text-transform: uppercase;
	}
	a{

		font-size: 22px;
		color: #000;


		&:after{
			content: '';
			display: inline-block;
			margin-left: 10px;
			@include sprite($ico-arro-look);
			vertical-align: middle;
		}
	}
}
.category-item__title{
	position: absolute;
	text-outline: 0;
	right: 0;
	top: -40px;
	font-family: $ave-bold;
	font-size: rem(23.33px);
	text-transform: uppercase;
	line-height: 30px;
	z-index: 30;
}

.category-item-wooman{
	padding-left: 60px;
		margin-top: 188px;
	
}
.category-item-wooman, .category-item-men, .category-item-accesories{
		min-height: 348px;

	.category-big-trapezoid{
		position: absolute;
		width: 89%;
		height: 100%;
		top: 0;
		border: 7px solid #000;
		transform: skewY(-7deg);
		
	}
	.category-small-trapezoid, .category-small-trapezoid__img{
		position: absolute;
	}
}
.category-item-wooman{
	.category-item-menu{
		top: -3%;
		left: 7%;
		z-index: 5;
	}
	.category-small-trapezoid{
		
		height: 379px;
		width: 90%;
		@include  absolute-center;
		top: -25%;
		border: 16px solid #84fd00;
		background-color: #fff;
	}
	.category-small-trapezoid__img{
	
		right: -5%;
		bottom: 2%;
		z-index: 3;

	}
}
.category-item-menu{
	position: relative;
	font-family: $ave-bold;
	z-index: 3;
}
.category-item-menu__title{
	display: inline-block;
	font-size: rem(30px);
	text-transform: uppercase;
	position: relative;

	&:after{
		content: '>>';
		position: absolute;
	@include absolute-center;
	left: auto;
	right: rem(-50px);
		margin-left: 17px;
	}
}
.category-item-menu__list{
	li{
		a{
			font-size: 14px;
			letter-spacing: 1.4px;
			text-decoration: none;
			text-transform: uppercase;
			color: #000;
			&:hover, &:active{
				text-decoration: underline;
				color: #70e400;
			}
			
		}

		&:last-child{
			a{
				text-decoration: underline;
			}
		}
	}
}
.category-item-men{
	margin-top: rem( 155px);
	min-height: 379px;
	.category-small-trapezoid__img{
		left: 2%;
		bottom: -7%;
		z-index: 2;
	}
	.category-item-menu{
		right: 32%;
		padding-top: 8%;
		z-index: 3;
		text-align: right;
	}
}

.category-item-accesories{
		padding-left: rem(60px);
		margin-top: -55px;
	.category-big-trapezoid{

		height: 366px;
		border: 3px solid #000;
		// position: relative;
		background-color: #fff;
		z-index: 10;
	}
	.category-small-trapezoid{
		height: 345px;
		width: 90%;
		left: 15%;
		top: -10%;
		border: 10px solid #000;
		z-index: 0;
		transform: skewY(-7deg);
	}
	.category-item-menu{
		right: 20%;
		padding-top: 8%;
		z-index: 20;
		text-align: right;
	}
	.category-small-trapezoid__img{
		position: absolute;
		top: -7%;
		left: -1px;
		transform: skew(-.3deg, 9deg);
	}
}
.category{
	.to-top{
		margin-top: 30px;
		margin-bottom: 58px;
		display: inline-block;
	}
}