.sidebar-menu__item{
	margin-bottom: 10px;
	position: relative;


	.sidebar-menu__link{
		font-family: $ave-bold;
		font-size: rem(12px);
		text-transform: uppercase;
		letter-spacing: 1.2px;
		color: #000;
		text-decoration: none;
	}
	&--active{
		.sidebar-menu__link{
			background-color: #88fb18;
		}
		&:before{
			transform: rotate(90deg);
		}
		.acordeon-sublist{
			display: block;
		}
	}
	&:hover{
		.sidebar-menu__link{
			background-color: #88fb18;
		}
	}
	&-with-ul{
		
		&:before{
			content: '';
			display: inline-block;
			// position: absolute;
			@include sprite($ico-category-arrow);
			margin-right: 6px;
			transition: all 0.5s;
		}
	}
}

.sidebar-menu__subitem{
	margin-left: 24px;
	a{
		font-family: $avenir-med;
		font-size: rem(14px);
		letter-spacing: 0.5px;
		text-transform: uppercase;
		color: #000;
	}
	// &:hover{
	// 	a{
	// 		background-color: transparent;
	// 	}
	// }
}
.acordeon-sublist{
	display: none;
}