.shop-products, .shop-looks{
	font-size: 0;
}
.shop-product__item{
	width: 33.33333333%;
	display: inline-block;
	vertical-align: top;
	padding: 0 15px;
}
.shop-looks{
	.shop-product__item{
		width: 50%;	
	}
	.shop-product__item-last{
		
		.shop-product__item-wrapper{
				height: 776px;
				
		}
	}
}
.shop-product__item-wrapper{
	margin-bottom: 12px;
	text-align: center;
	background-color: #f1f1f1;

}
.shop-product__item-last{
		position: relative;	
		background-color: transparent;
	.shop-product__item-wrapper{
			height: 533px;
			position: relative;	
	}
}
.shop-product__item-last-inner{
		background-color: $footer-bg;

}
.shop-product__item-img-wrap{
	img{
		width: 100%;
	}
}
.shop-product__item-bottom{
	padding: 9px 0;
	line-height: rem(24px);
}
.shop-product__item-designer {
	font-size: rem(18px);
	font-family: $ave-bold;
	text-transform: uppercase;
	letter-spacing: 1.4px;
}
.shop-product__item-look {
	@extend .shop-product__item-designer;
}
.shop-product__item-stylist{
	@extend .shop-product__item-product;
}
.shop-product__item-product {
	font-size: rem(18px);
	font-family: $avenir-med;
	

}
.shop-product__item-price {
	font-size: rem(16px);
	font-family: $avenir-med;
	

}
.pagination{
	width: 100%;
	margin-top: rem(26px);
	text-align: center;
	ul{
		display: inline-block;

		li{
			display: inline-block;
			padding: 0 rem(19px);
			margin: 0 rem(21px);
			position: relative;
			
			&:after{
				content: '';
				display: none;
				position: absolute;
				// display: block;
				width: 100%;
				height: 9px;
				background-color: #000;
				bottom: -9px;
				right: 0;
				left: 0;
			}
			&.pagination-active{
				&:after{
					display: block;
				}
			}
		}
		a{
		font-family: $avenir-med;
		font-size: rem(24px);
		color: #000;
		text-decoration: none;
		}
	}
}
.pagination__line{
	height: 9px;
	margin-bottom: rem(25px);
	background-color: #f1f1f1;
}
.to-top{
	display: inline-block;
	margin-bottom: rem(44px);
	font-size: rem(14px);
	text-transform: uppercase;
	color: #808080;
	&:hover, &:active, &:focus{
		color: #808080;
	}
		
}
.ico-chevron-up{
	@include sprite($ico-chevron-up);
	display: inline-block;
	margin-left: 15px;
	vertical-align: middle;
}